.hero {
  padding: 134px 0;
  margin-bottom: 709px;

  &_container {
    display: flex;
    justify-content: center;
  }

  &__content {
    max-width: 950px;
    display: flex;
    flex-direction: column;
  }

  &__subtitle {
    display: flex;
    align-items: center;
    margin: 0;
    margin-bottom: 32px;
    font-weight: 800;
    font-size: 18px;
    letter-spacing: 6px;
    text-transform: uppercase;
    color: var(--accent-color);

    &::before {
      content: "";
      display: block;
      width: 72px;
      height: 2px;
      background-color: var(--accent-color);
      margin-right: 32px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 32px;
    font-family: 'Chronicle';
    font-weight: 600;
    font-size: 88px;
    line-height: 100px;
    text-transform: capitalize;
    color: var(--light-color);
  }

  &__btn {
    display: flex;
    align-items: center;
    width: fit-content;

    &:hover > img {
      transform: translateY(7.5px);
    }
  }

  &__btn-text {
    margin: 0;
    margin-right: 15px;
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    color: var(--light-color);
  }

  &__btn-img {
    transition: all .3s ease-in-out;
  }
}
