.footer {
  height: 400px;

  &__container {
    display: flex;
    justify-content: space-between;
  }

  &__descr {
    display: flex;
    flex-direction: column;
  }

  &__logo {
    margin: 0;
    margin-bottom: 24px;
  }

  &__text {
    margin: 0;
    margin-bottom: 122px;
    max-width: 303px;
    font-weight: 700;
    font-size: 18px;
    line-height: 32px;
    color: var(--light-color);
  }

  &__copy {
    font-weight: 500;
    font-size: 18px;
    line-height: 32px;
    color: rgba(255, 255, 255, 0.5);
  }

  &__nav {
    display: flex;
  }

  &__list {
    display: flex;
    flex-direction: column;

    &:last-child {
      margin-left: 212px;
    }

    &-title {
      margin: 0;
      margin-bottom: 24px;
      font-weight: 700;
      font-size: 24px;
      line-height: 32px;
      color: var(--accent-color);
    }
  }

  &__item {
    margin: 0;
    margin-bottom: 16px;
  }

  &__link {
    font-weight: 500;
    font-size: 18px;
    line-height: 32px;
    color: var(--light-color);
  }
}
