html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
  background-color: var(--dark-color);
  background-image: url(../img/bg-img.jpg);
  background-size: 100% auto;
  background-repeat: no-repeat;
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.container-fluid {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-fluid-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.header {
  padding: 64px 0;
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__login {
  display: flex;
  align-items: center;
}
.header__login-img {
  margin-right: 10px;
}
.header__login-text {
  margin: 0;
  font-size: 17px;
  color: var(--light-color);
}

.nav__list {
  display: flex;
  align-items: center;
}
.nav__item:not(:last-child) {
  margin-right: 40px;
}
.nav__link {
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  color: var(--light-color);
}

.hero {
  padding: 134px 0;
  margin-bottom: 709px;
}
.hero_container {
  display: flex;
  justify-content: center;
}
.hero__content {
  max-width: 950px;
  display: flex;
  flex-direction: column;
}
.hero__subtitle {
  display: flex;
  align-items: center;
  margin: 0;
  margin-bottom: 32px;
  font-weight: 800;
  font-size: 18px;
  letter-spacing: 6px;
  text-transform: uppercase;
  color: var(--accent-color);
}
.hero__subtitle::before {
  content: "";
  display: block;
  width: 72px;
  height: 2px;
  background-color: var(--accent-color);
  margin-right: 32px;
}
.hero__title {
  margin: 0;
  margin-bottom: 32px;
  font-family: "Chronicle";
  font-weight: 600;
  font-size: 88px;
  line-height: 100px;
  text-transform: capitalize;
  color: var(--light-color);
}
.hero__btn {
  display: flex;
  align-items: center;
  width: fit-content;
}
.hero__btn:hover > img {
  transform: translateY(7.5px);
}
.hero__btn-text {
  margin: 0;
  margin-right: 15px;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  color: var(--light-color);
}
.hero__btn-img {
  transition: all 0.3s ease-in-out;
}

.social {
  display: flex;
  flex-direction: column;
  max-width: 24px;
  height: 173px;
  position: fixed;
  top: 40%;
  left: 80px;
  z-index: 500;
}
.social__text {
  display: block;
  margin: 0;
  width: fit-content;
  height: fit-content;
  white-space: pre;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  color: var(--light-color);
  transform-origin: 10px;
  transform: rotate(90deg);
}
.social__list {
  margin-top: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.social__item {
  margin-top: 26px;
}

.content__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.content__article {
  margin-bottom: 200px;
  position: relative;
  width: 100%;
  max-height: 720px;
  display: flex;
  justify-content: space-between;
}
.content__article-unic .content__img {
  margin-left: 0;
}
.content__block {
  position: relative;
}
.content__number {
  user-select: none;
  position: absolute;
  margin: 0;
  top: 40px;
  left: 0;
  font-weight: 700;
  font-size: 240px;
  line-height: 240px;
  color: rgba(255, 255, 255, 0.1);
  z-index: 5;
}
.content__descr {
  margin: 140px 0 0 150px;
  max-width: 632px;
  z-index: 10;
}
.content__subtitle {
  margin: 0;
  margin-bottom: 35px;
  display: flex;
  align-items: center;
  font-weight: 800;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 6px;
  text-transform: uppercase;
  color: var(--accent-color);
}
.content__subtitle::before {
  content: "";
  margin-right: 24px;
  display: block;
  width: 72px;
  height: 2px;
  background-color: var(--accent-color);
}
.content__title {
  margin: 0;
  max-width: 555px;
  margin-bottom: 27px;
  font-family: "Chronicle";
  font-style: normal;
  font-weight: 600;
  font-size: 64px;
  line-height: 77px;
  color: var(--light-color);
}
.content__text {
  margin: 0;
  margin-bottom: 27px;
  font-weight: 700;
  font-size: 18px;
  line-height: 32px;
  color: var(--light-color);
}
.content__btn {
  display: flex;
  align-items: center;
  width: fit-content;
}
.content__btn > img {
  transition: all 0.3s ease-in-out;
}
.content__btn:hover > img {
