}

.footer {
  height: 400px;
}
.footer__container {
  display: flex;
  justify-content: space-between;
}
.footer__descr {
  display: flex;
  flex-direction: column;
}
.footer__logo {
  margin: 0;
  margin-bottom: 24px;
}
.footer__text {
  margin: 0;
  margin-bottom: 122px;
  max-width: 303px;
  font-weight: 700;
  font-size: 18px;
  line-height: 32px;
  color: var(--light-color);
}
.footer__copy {
  font-weight: 500;
  font-size: 18px;
  line-height: 32px;
  color: rgba(255, 255, 255, 0.5);
}
.footer__nav {
  display: flex;
}
.footer__list {
  display: flex;
  flex-direction: column;
}
.footer__list:last-child {
  margin-left: 212px;
}
.footer__list-title {
  margin: 0;
  margin-bottom: 24px;
  font-weight: 700;
  font-size: 24px;
  line-height: 32px;
  color: var(--accent-color);
}
.footer__item {
  margin: 0;
  margin-bottom: 16px;
}
.footer__link {
  font-weight: 500;
  font-size: 18px;
  line-height: 32px;
  color: var(--light-color);
}