.social {
  display: flex;
  flex-direction: column;
  max-width: 24px;
  height: 173px;
  position: fixed;
  top: 40%;
  left: 80px;
  z-index: 500;

	&__text {
    display: block;
    margin: 0;
    width: fit-content;
    height: fit-content;
    white-space: pre;
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    color: var(--light-color);
    transform-origin: 10px;
    transform: rotate(90deg);
	}

	&__list {
    margin-top: auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
	}

	&__item {
    margin-top: 26px;
	}
}
