.content {

  &__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__article {
    margin-bottom: 200px;
    position: relative;
    width: 100%;
    max-height: 720px;
    display: flex;
    justify-content: space-between;

    &-unic .content__img {
      margin-left: 0;
    }
  }

  &__block {
    position: relative;
  }

  &__number {
    user-select: none;
    position: absolute;
    margin: 0;
    top: 40px;
    left: 0;
    font-weight: 700;
    font-size: 240px;
    line-height: 240px;
    color: rgba(255, 255, 255, 0.1);
    z-index: 5;
  }

  &__descr {
    margin: 140px 0 0 150px;
    max-width: 632px;
    z-index: 10;
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 35px;
    display: flex;
    align-items: center;
    font-weight: 800;
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 6px;
    text-transform: uppercase;
    color: var(--accent-color);

    &::before {
      content: "";
      margin-right: 24px;
      display: block;
      width: 72px;
      height: 2px;
      background-color: var(--accent-color);
    }
  }

  &__title {
    margin: 0;
    max-width: 555px;
    margin-bottom: 27px;
    font-family: 'Chronicle';
    font-style: normal;
    font-weight: 600;
    font-size: 64px;
    line-height: 77px;
    color: var(--light-color);
  }

  &__text {
    margin: 0;
    margin-bottom: 27px;
    font-weight: 700;
    font-size: 18px;
    line-height: 32px;
    color: var(--light-color);
  }

  &__btn {
    display: flex;
    align-items: center;
    width: fit-content;

    & > img {
      transition: all .3s ease-in-out;
    }

    &:hover > img {
      transform: translateX(7.5px);
    }
  }

  &__btn-text {
    margin: 0;
    margin-right: 16px;
    font-size: 18px;
    line-height: 22px;
    color: var(--accent-color);
  }

  &__img {
    margin-left: 114px;
    user-select: none;
  }
}
